@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
   h1 {
      @apply font-bold;
      @apply text-lg;
   }

   a {
    @apply mx-5;
    @apply text-slate-100;
    @apply hover:text-rose-300;
   } 
}

.skew {
   background: linear-gradient(2deg, rgba(255,255,255,1) 10%, rgba(30,41,59,1) 10%, rgba(30,41,59,1) 90%, rgba(255,255,255,1) 90%);
}
